@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,700;1,700&family=Roboto:ital@0;1&display=swap');

input[type=text], select, textarea {
  width: 100%;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  margin-top: 6px;
  margin-bottom: 16px;
  resize: vertical;
  font-family: Roboto;
}

input[type=submit] {
  background-color: rgb(238, 147, 37);
  color: white;
  padding: 12px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

input[type=submit]:hover {
  background-color: rgb(238, 147, 37);
}

.container-contact {
  border-radius: 5px;
  background-color: #f2f2f2;
  padding: 20px;
  display: grid;
  grid-template-columns: repeat(1fr);
}

.contact-label {
  font-family: Montserrat;
}